
@import '../../global/box-container.scss';
.container {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-auto-columns: 1fr;
  gap: 0px 0px;
  grid-auto-flow: row;
  grid-template-areas:
    "head"
    "contenido"
    "footer";
  width: 100%;
  height: 100vh;
}

.head {
  grid-area: head;
  height: fit-content;
}

.content {
  position: relative;
  grid-area: contenido;
  height: calc(100vh - 100px);
  min-width: 100%;
  padding: 0;
  overflow-y: auto;
  
}

.footer {
  grid-area: footer;
  background-color: transparent;
  padding: 0;
  //padding-bottom: 16px;
}
